@use "../../assets/scss/mixins" as *;
@use "../../assets/scss/variables" as *;
@use "../../assets/scss/font" as *;

.product-md {
  @include ko-Body-Large;
  background-color: var(--color-main);
  .product-detail {
    display: grid;
    grid-template-columns: 1fr 1fr;

    // 이미지 초기 상태
    img {
      width: 100%;
      height: 480px;      
      object-fit: cover;
      @include mobile{ height: 220px;}
    }
    .product-txt {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      padding: 3rem 6rem;
      gap: 1rem;
      @include mobile{
        padding: 1rem 1rem;
        gap: 0.5rem;
        word-break: keep-all;
      }
      > h3,
      > p,
      > h4,
      > a {
        opacity: 0;
        transform: translateY(20px);
        transition: opacity 0.6s ease, transform 0.6s ease;
        @include mobile{ font-size: 0.8rem;}
      }
      h3 {
        font-size: 2.5rem;
        @include mobile{font-size: 1.5rem}
      }
      button {
        @include btn-style(var(--color-BK));
        border-radius: 2rem;
        font-size: 1.2rem;
        @include mobile{ font-size: 0.6rem;}
        &:hover{
          background-color: rgba(255,255,255,0.5);
        }
      }
    }
  }
  //   스크롤 후 활성화
  &.is-visible {
    .product-detail {


      .product-txt {
        > h3,
        > p,
        > h4,
        > a {
          opacity: 1;
          transform: translateY(0);
        }

        /* 텍스트 순차 등장 */
        > *:nth-child(1) {
          transition-delay: 0.5s; // h3
        }

        > *:nth-child(2) {
          transition-delay: 0.8s; // p
        }

        > *:nth-child(3) {
          transition-delay: 1.2s; // h4
        }

        > *:nth-child(4) {
          transition-delay: 1.2s; // button (Link 포함)
        }
      }
    }
  }
}